import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Http, Response } from '@angular/http';
/**
 * Generated class for the DiscoveryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-discovery',
  templateUrl: 'discovery.html',
})
export class DiscoveryPage {
  discovery: String = "text";
  textData: Object = {
    code: undefined,
    msg: "",
    data: []
  };
  picData: Object = {
    code: undefined,
    msg: "",
    data: []
  };
  videoData: Object = null;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private http: Http
    ) {
  }

  updateTextData(){
    this.http.request('https://www.apiopen.top/satinApi?type=2&page=1')
    .subscribe((res: Response) => {
      this.textData = res.json();
    });
  }
  updatePicData(){
    this.http.request('https://www.apiopen.top/satinApi?type=3&page=1')
    .subscribe((res: Response) => {
      this.picData = res.json();
    });
  }
  updateVideoData(){
    this.http.request('https://www.apiopen.top/satinApi?type=4&page=1')
    .subscribe((res: Response) => {
      this.videoData = res.json();
    });
  }
  
  ionViewDidLoad() {
    console.log('ionViewDidLoad DiscoveryPage');
    this.http.request('https://www.apiopen.top/satinApi?type=2&page=1')
    .subscribe((res: Response) => {
      this.textData = res.json();
    });
  }
  

}



